:root {
  color-scheme: light;
}

* {
  box-sizing: border-box;
}

html, body {
  padding: 0;
  margin: 0;
  font-family: "Inter", sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  width: min(100% - 2rem, 1200px);
  margin-inline: auto;
}

.pad {
  padding-block: 3rem;
}

.grid-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
}

@media (max-width: 900px) {
  .grid-2, .grid-3 {
    grid-template-columns: 1fr;
  }
}
.site-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  background: transparent;
  border: none;
}

.secondary {
  position: relative;
  background-color: #1f1529;
}

.brand img {
  max-width: 25%;
}

.site-header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 72px;
  gap: 1rem;
  border-block-end: 1px solid #ffffff;
}

.brand--logo {
  text-decoration: none;
  color: inherit;
}

.brand__text {
  font-weight: 900;
  letter-spacing: 0.3px;
  font-size: 1.25rem;
  color: #0f172a;
}

.brand__tag {
  color: #64748b;
  font-weight: 400;
  margin-left: 0.5rem;
}

.menu {
  display: flex;
  align-items: center;
  gap: 1rem;
}

/* === Enlaces normales (sin clase) === */
.menu a:not(.btn) {
  position: relative;
  text-decoration: none;
  color: #ffffff;
  padding: 0.5rem 0.8rem;
  transition: transform 0.3s ease-in-out;
}

.menu a:not(.btn)::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 2px;
  width: 0%;
  background: #005AFF;
  transition: width 0.3s ease-in-out;
}

.menu a:not(.btn):hover {
  transform: translateX(4px);
}

.menu a:not(.btn):hover::after {
  width: 100%;
}

/* ===  Bot√≥n especial (.btn) === */
.menu .btn {
  background-color: #005AFF;
  padding: 0.5rem 1rem;
  border-radius: 999px;
  transition: background 0.3s ease-in-out, transform 0.3s ease-in-out;
  color: #ffffff;
  text-decoration: none;
}

/* Efecto distinto para el bot√≥n: brillo al pasar */
.menu .btn::before {
  content: "";
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  transition: left 0.4s ease-in-out;
  border-radius: inherit;
}

.menu .btn:hover {
  background-color: #1f1529;
  transform: scale(1.05);
}

.menu .btn:hover::before {
  left: 100%; /* brillo pasa de izquierda a derecha */
}

.nav-toggle {
  display: none;
}

@media (max-width: 900px) {
  .nav-toggle {
    display: block;
    background: none;
    border: 1px solid #ccc;
    padding: 0.4rem 0.6rem;
    border-radius: 8px;
  }
  .menu {
    position: fixed;
    inset: 72px 0 auto 0;
    background: #ffffff;
    padding: 1rem;
    border-bottom: 1px solid #ccc;
    transform: translateY(-120%);
    transition: 0.25s;
    flex-direction: column;
    align-items: flex-start;
  }
  .menu.open {
    transform: translateY(0);
  }
}
.card {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 1.25rem;
}

.site-footer {
  margin-top: 3rem;
  background: #0f0f11;
  color: #ddd;
  padding-block: 2rem;
}

.site-footer .brand {
  color: #ffffff;
  font-weight: 900;
}

.site-footer h4 {
  margin: 0 0 0.5rem;
  color: #ffffff;
}

.site-footer .list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: grid;
  gap: 0.4rem;
}

.site-footer a {
  color: #ddd;
  text-decoration: none;
}

.copy {
  text-align: center;
  border-top: 1px solid rgba(255, 255, 255, 0.08);
  margin-top: 1rem;
  padding-top: 1rem;
  font-size: 0.9rem;
  color: #aaa;
}

/* ===== Footer visual sobre el mismo fondo azul ===== */
.site-tail {
  position: relative;
  overflow: hidden;
  padding-block: clamp(0.5rem, 1vw, 0.5rem);
  color: #ffffff;
  background: radial-gradient(1200px 600px at 25% -10%, rgba(255, 255, 255, 0.06), rgba(255, 255, 255, 0) 60%), radial-gradient(900px 500px at 80% 30%, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0) 60%), linear-gradient(180deg, #0f58a4 0%, #0d4f93 100%);
}

.site-tail__row {
  display: grid;
  grid-template-columns: 1fr auto auto;
  align-items: center;
  gap: clamp(1rem, 3vw, 2rem);
}

.tail__brand img {
  display: block;
  height: 58px; /* ajusta al alto de tu logo de referencia */
  width: auto;
}

/* Centro: Follow + redes */
.tail__social {
  display: flex;
  align-items: center;
  gap: clamp(0.6rem, 1.8vw, 1.2rem);
}

.follow__arrow {
  height: 86px;
  width: auto;
}

.follow__badge {
  height: 36px;
  width: auto;
}

.socials {
  display: flex;
  align-items: center;
  gap: 0.8rem;
}

/* Botones circulares */
.social {
  --size: 52px;
  width: var(--size);
  height: var(--size);
  border-radius: 999px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: #ffffff;
  color: #fac532;
  text-decoration: none;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.15);
  transition: transform 0.2s ease, box-shadow 0.2s ease, color 0.2s ease, background 0.2s ease;
}

.social:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.22);
  background: #3886c9;
  color: #ffffff;
}

/* Bot√≥n pill de privacidad */
.privacy-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.6rem;
  background: #111;
  color: #fff;
  padding: 0.7rem 1.2rem;
  border-radius: 999px;
  text-decoration: none;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.25);
  transition: transform 0.2s ease, box-shadow 0.2s ease, background 0.2s ease;
}

.privacy-btn:hover {
  transform: translateY(-2px);
  background: #000;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.32);
}

/* L√≠nea inferior + direcci√≥n */
.tail__address {
  margin-top: clamp(1rem, 3vw, 1.6rem);
  padding-top: 0.8rem;
  border-top: 1px dashed rgba(255, 255, 255, 0.35);
  text-align: center;
  font-size: 0.9rem;
  letter-spacing: 0.06em;
  color: #e0eeff;
}

/* Responsive */
@media (max-width: 1100px) {
  .site-tail__row {
    grid-template-columns: 1fr;
    gap: 1.2rem;
    text-align: center;
  }
  .tail__social {
    justify-content: center;
    flex-wrap: wrap;
  }
  .privacy-btn {
    justify-self: center;
  }
  .tail__brand img {
    margin-inline: auto;
  }
}
.hero {
  position: relative;
}

.hero--cover {
  min-height: clamp(420px, 75vh, 680px);
}

.hero--cover::before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 2;
  background: linear-gradient(90deg, #294e85, rgba(0, 0, 0, 0.05));
}

.hero__overlay {
  position: absolute;
  inset: 0;
}

.hero__content {
  position: relative;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-height: inherit;
}

.hero__content h1 {
  color: #ffffff;
  font-weight: 700;
  font-size: clamp(2rem, 4.6vw, 3.5rem);
  line-height: 1.3;
  max-width: 29ch;
  text-shadow: 0 2px 18px rgba(0, 0, 0, 0.25);
}

/* ===== Slides de fondo ===== */
.hero__slides {
  position: absolute;
  inset: 0;
  z-index: 1;
  overflow: hidden;
}

.hero__slide {
  position: absolute;
  inset: 0;
  background: var(--img) center/cover no-repeat;
  opacity: 0;
  transform: scale(1.1); /* inicia un poco m√°s grande para el fade-in si quieres */
  transition: opacity 1s ease, transform 6s ease; /* suavidad al entrar y efecto de zoom */
  will-change: opacity, transform;
}

/* Ken Burns para la slide activa */
@keyframes hero-kenburns {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.15);
  }
}
.hero__slide.is-active {
  opacity: 1;
  animation: hero-kenburns 7s ease-in-out both; /* ‚Äúacercamiento‚Äù suave */
  transform: scale(1); /* punto de partida */
}

/* Accesibilidad: reduce motion */
@media (prefers-reduced-motion: reduce) {
  .hero__slide {
    transition: none;
    transform: none;
  }
  .hero__slide.is-active {
    animation: none;
  }
}
.pill-strip {
  z-index: 4;
  position: relative;
  margin-top: -3.2rem;
  margin-bottom: 1.5rem;
}

.pill {
  background: #fff;
  border: 1px solid #ccc;
  border-radius: 1rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
  padding: 1.25rem;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1rem;
  border-radius: 2.5rem;
  padding: 1rem 1.25rem;
  align-items: center;
}

.pill__item {
  display: flex;
  align-items: center;
  gap: 0.8rem;
  padding: 0.6rem 1rem;
}

.pill__icon {
  width: 48px;
  height: 48px;
  border-radius: 999px;
  display: grid;
  place-items: center;
  background: #005AFF;
  color: #ffffff;
}

.pill__title {
  font-weight: 700;
  line-height: 1;
}

.pill__subtitle {
  color: #6b7280;
  font-size: 0.95rem;
  margin-top: 0.15rem;
}

@media (max-width: 900px) {
  .pill {
    grid-template-columns: 1fr;
    border-radius: 1.25rem;
  }
}
/* ===== About ===== */
.about.pad {
  padding-block: clamp(1rem, 2vw, 1.8rem);
}

.about__grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: clamp(0.6rem, 1.5vw, 1rem); /* m√°s cerrado */
  align-items: start;
}

.about__media {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: auto auto;
  gap: 1rem;
}

/* Imagen alta a la izquierda, dos im√°genes apiladas a la derecha */
.about__img {
  margin: 0;
  border-radius: 18px;
  overflow: hidden;
  background: #ddd;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.08);
}
.about__img img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.about__img--tall {
  grid-column: 1/2;
  grid-row: 1/3;
  min-height: clamp(320px, 48vh, 500px);
  overflow: hidden;
}

.about__img--tall img {
  transition: transform 0.6s cubic-bezier(0.25, 0.1, 0.25, 1);
  transform-origin: center center;
}

.about__img--tall:hover img {
  transform: scale(1.08);
}

.about__img--top {
  grid-column: 2/3;
  grid-row: 1/2;
  min-height: clamp(160px, 24vh, 230px);
}

.about__img--bottom {
  grid-column: 2/3;
  grid-row: 2/3;
  min-height: clamp(160px, 24vh, 230px);
}

/* Badge circular azul centrado entre im√°genes */
.xp-badge {
  position: absolute;
  left: 48%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: clamp(120px, 16vw, 180px);
  height: clamp(120px, 16vw, 180px);
  border-radius: 999px;
  background: #005AFF; /* azul principal de tu marca */
  color: #ffffff;
  place-items: center;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.12), 0 6px 18px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  justify-content: center; /* centra ambos en el eje vertical */
  align-items: center;
  text-align: center;
}

.xp-badge__num {
  font-weight: 900;
  font-size: clamp(2rem, 4vw, 3rem);
}
.xp-badge__num span {
  font-weight: 700;
  margin-left: 0.1em;
}
.xp-badge__num {
  line-height: 1; /* evita el espacio vertical exagerado */
  margin-bottom: 0.2rem; /* ajusta la distancia manualmente */
}

.xp-badge__txt {
  font-size: clamp(0.8rem, 1.2vw, 0.95rem);
  opacity: 0.95;
  line-height: 1.1; /* m√°s compacto */
  opacity: 0.95;
}

/* Columna derecha: t√≠tulo, l√≠nea divisoria y copy */
.about__content {
  /* l√≠nea vertical sutil como en la imagen */
  border-left: 2px solid rgba(0, 0, 0, 0.08);
  padding-left: clamp(0.6rem, 2vw, 1rem);
}

.about__content_R {
  /* l√≠nea vertical sutil como en la imagen */
  border-right: 2px solid rgba(0, 0, 0, 0.08);
  padding-right: clamp(0.6rem, 2vw, 1rem);
}

.about__content h2 {
  margin: 0 0 0.8rem 0;
  font-size: clamp(1.6rem, 3vw, 2.2rem);
  font-weight: 900;
}

.about__copy {
  display: grid;
  gap: none;
  color: #192231; /* texto oscuro legible */
  padding: 0%;
}

.about__cta {
  margin-top: 1.2rem;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  box-shadow: 0 10px 24px rgba(17, 91, 240, 0.25); /* halo azul */
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.about__cta:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 30px rgba(17, 91, 240, 0.32);
}

/* Responsive */
@media (max-width: 1024px) {
  .about__grid {
    grid-template-columns: 1fr;
  }
  .about__content {
    border-left: 0;
    padding-left: 0;
  }
  .xp-badge {
    left: 50%;
    top: 50%;
  }
}
/* ===== Locations===== */
.loc {
  position: relative;
  color: #ffffff;
  overflow: hidden;
  /* espacio vertical moderado */
  padding-block: clamp(2rem, 6vw, 4rem);
  background: linear-gradient(90deg, #0b3e74 0%, #052b52 100%);
}

/* Capa extra de curvas muy sutiles (no imagen) */
.loc__bg {
  position: absolute;
  inset: 0;
  pointer-events: none;
  background: conic-gradient(from 210deg at 40% 0%, rgba(255, 255, 255, 0.05), transparent 60%), conic-gradient(from 10deg at 95% 40%, rgba(255, 255, 255, 0.05), transparent 60%);
  mix-blend-mode: soft-light;
  opacity: 0.8;
}

.loc__grid {
  display: grid;
  grid-template-columns: 1.05fr 0.95fr; /* texto / mapa */
  gap: clamp(1rem, 3.5vw, 3rem);
  align-items: center;
}

.loc__text h2 {
  margin: 0 0 1rem 0;
  font-weight: 900;
  font-size: clamp(1.8rem, 4.2vw, 2.5rem);
}

/* l√≠nea vertical sutil como en la referencia */
.loc__copy {
  position: relative;
  display: grid;
  gap: 0.9rem;
  max-width: 64ch;
}

.loc__copy::before {
  content: "";
  position: absolute;
  left: -1.5rem; /* separada del texto */
  top: 0;
  bottom: 0;
  width: 2px;
  background: rgba(255, 255, 255, 0.22);
  border-radius: 2px;
}

/* tarjeta del mapa */
.loc__media {
  margin: 0; /* quitar margen de figure */
  background: rgba(0, 0, 0, 0.22);
  border-radius: 22px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.25);
  overflow: hidden;
  padding: clamp(0.3rem, 0.8vw, 0.6rem); /* marco sutil */
}

.loc__media img {
  display: block;
  width: 100%;
  height: auto;
  border-radius: 18px;
  /* si tu imagen es muy ancha/alta y quieres recorte uniforme:
     height: 100%; object-fit: cover; aspect-ratio: 16/9; */
}

/* Responsive */
@media (max-width: 1024px) {
  .loc__grid {
    grid-template-columns: 1fr;
  }
  .loc__copy::before {
    left: -1rem;
  }
}
.strategy {
  position: relative;
  overflow: hidden;
  color: #ffffff;
  padding: 0%;
}

.strategy__grid {
  display: grid;
  grid-template-columns: 1.7fr 2fr;
  align-items: center;
  min-height: clamp(400px, 70vh, 580px);
  position: relative;
}

.strategy__img {
  position: relative;
  background: url("/build/img/strategy-bg.webp") center/cover no-repeat;
  filter: blur(0.8px);
  height: 100%;
  width: 100%;
}

/* Capa azul transl√∫cida */
.strategy__overlay {
  content: "";
  position: absolute;
  inset: 0;
  mix-blend-mode: multiply;
  z-index: 1;
}

/* Contenedor de texto */
.strategy__text {
  position: relative;
  z-index: 2;
  padding-inline: clamp(1rem, 4vw, 4rem);
}

.strategy__text h2 {
  font-size: clamp(1.4rem, 3.2vw, 2.8rem);
  font-weight: 900;
  line-height: 1.3;
  color: #ffffff;
}

/* Responsive */
@media (max-width: 1024px) {
  .strategy__grid {
    grid-template-columns: 1fr;
  }
  .strategy__text {
    padding-block: 2rem;
    text-align: center;
  }
  .strategy__text h2 {
    max-width: none;
  }
}
.boost {
  position: relative;
  min-height: clamp(360px, 62vh, 560px);
  overflow: hidden;
  color: #ffffff;
}

.boost__video {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  filter: contrast(1.05) saturate(1.05);
  z-index: 0;
}

/* velo/overlay azul tenue */
.boost__overlay {
  position: absolute;
  inset: 0;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.15), rgba(0, 0, 0, 0.35)), linear-gradient(120deg, rgba(17, 91, 240, 0.25), rgba(17, 91, 240, 0));
  z-index: 1;
}

.boost__content {
  position: relative;
  z-index: 2;
  min-height: inherit;
  display: grid;
  place-items: center;
  padding-block: clamp(1rem, 4vw, 2.5rem);
}

/* burbuja transl√∫cida con blur y esquinas grandes */
.boost__bubble {
  max-width: 68ch;
  background: rgba(15, 23, 42, 0.35); /* oscuro transl√∫cido */
  color: #ffffff;
  border-radius: 24px;
  padding: clamp(1rem, 3.5vw, 2rem);
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.18);
  backdrop-filter: blur(6px); /* si el navegador lo soporta */
  -webkit-backdrop-filter: blur(6px);
}

.boost__bubble h2 {
  margin: 0;
  font-weight: 900;
  font-size: clamp(1.25rem, 3vw, 2rem);
  line-height: 1.25;
}

/* Accesibilidad: si el usuario prefiere menos animaci√≥n, pausa video */
@media (prefers-reduced-motion: reduce) {
  .boost__video {
    animation: none;
  }
  .boost__video[autoplay] {
    opacity: 0.95;
  }
}
/* ===== Founders Section ===== */
.founders {
