.hero__content h1 {
  color: #ffffff;
  font-weight: 700;
  font-size: clamp(2rem, 4.6vw, 3.5rem);
  line-height: 1.3;
  max-width: 29ch;
  text-shadow: 0 2px 18px rgba(0, 0, 0, 0.25);
  /* üîΩ Animaci√≥n */
  opacity: 0;
  transform: translateY(40px);
  animation: fadeUp 1.2s ease-out forwards;
  animation-delay: 0.4s;
}

@keyframes fadeUp {
  0% {
    opacity: 0;
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
/* ===== Animaci√≥n de entrada tipo "desde atr√°s" ===== */
.pill {
  opacity: 0;
  transform: scale(0.8);
  animation: zoomIn 1.2s cubic-bezier(0.22, 1, 0.36, 1) forwards;
  animation-delay: 1s; /* entra justo despu√©s del t√≠tulo */
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0.8);
  }
  100% {
    opacity: 1;
    transform: scale(1);
  }
}
/* Opcional: efecto secuencial en cada pill__item */
.pill__item {
  opacity: 0;
  transform: translateZ(-100px);
  animation: appearForward 0.8s ease-out forwards;
}

.pill__item:nth-child(1) {
  animation-delay: 1.2s;
}

.pill__item:nth-child(2) {
  animation-delay: 1.4s;
}

.pill__item:nth-child(3) {
  animation-delay: 1.6s;
}

@keyframes appearForward {
  0% {
    opacity: 0;
    transform: translateZ(-100px) scale(0.9);
  }
  100% {
    opacity: 1;
    transform: translateZ(0) scale(1);
  }
}
/* Opcional: que se vean bien los n√∫meros */
@property --badge-scale {
  syntax: "<number>";
  inherits: false;
  initial-value: 1;
}
.xp-badge__num {
  font-variant-numeric: tabular-nums;
  -webkit-font-smoothing: antialiased;
}

/* Zoom al completar el conteo */
@keyframes badgePulse {
  0% {
    --badge-scale: 1;
  }
  50% {
    --badge-scale: 1.08;
  }
  100% {
    --badge-scale: 1;
  }
}
.xp-badge.pulse-zoom {
  animation: badgePulse 0.5s ease-out;
  will-change: transform;
}

/* Respeta usuarios con motion reducido */
@media (prefers-reduced-motion: reduce) {
  .xp-badge.pulse-zoom {
    animation: none;
  }
}
.Colum_left,
.Colum_right {
  opacity: 0;
  transform: translateX(0);
  transition: all 1s ease-out;
}

/* Posiciones iniciales */
.Colum_left {
  transform: translateX(-80px);
}

.Colum_right {
  transform: translateX(80px);
}

/* Cuando es visible */
.Colum_left.visible,
.Colum_right.visible {
  opacity: 1;
  transform: translateX(0);
}